import { utils } from '../common/Utils';
import { ServiceConfig } from '../common/ServiceConfig';
import gameType from '../mock/gameTypeDecorator';
import leftMenu from '../mock/leftMenuDecorator';
import gameList from '../mock/gameListDecorator';
import activityList from '../mock/activityListDecorator';
class Service {

  /********** 
 *左侧菜单
***********/
  @leftMenu.queryAccountMenu
  async queryAccountMenu() {
    return await utils.requestNoLogin({
      url: ServiceConfig.interface.queryAccountMenu
    })
  }

  /***************
   *游戏广场顶部栏分类
   ****************/
  @gameType.queryAllGameType
  async queryAllGameType() {
    return await utils.requestNoLogin({
      url: ServiceConfig.interface.queryAllGameType
    });
  }

  /***************
   *游戏广场游戏列表
   ****************/
  @gameList.queryGameList
  async queryGameList(data) {
    return await utils.requestNoLogin({
      url: ServiceConfig.interface.queryGameList,
      data: data || {}
    });
  }

  /***************
 *我的活动列表
 ****************/
  @activityList.queryActivityList
  async queryActivityList(data) {
    return await utils.request({
      url: ServiceConfig.interface.queryActivityList,
      data: data || {}
    });
  }

  /***************
*删除活动
****************/
  @activityList.delActivity
  async delActivity(data) {
    return await utils.request({
      url: ServiceConfig.interface.delActivity,
      data: data || {}
    });
  }

  /***************
 *登录
 ****************/
  async getPassport(data) {
    return await utils.requestNoLogin({
      url: ServiceConfig.interface.passporturl,
      data: data
    });
  }

  /***************
 *创建弹窗
 ****************/
  // 
  async getEsProp(data) {
    return await utils.request({
      url: ServiceConfig.interface.esPopupurl,
      data: data,
      callback: "esPropCallback"
    });
  }
  /***************
 *草稿活动code
 ****************/

  async getDraftMoveCode(data) {
    return await utils.request({
      url: ServiceConfig.interface.draftMove,
      data: data,
      callback: "draftMoveCodeCallback"
    });
  }
  /***************
*预览弹窗
****************/
  async getPreProp(data) {
    return await utils.request({
      url: ServiceConfig.interface.prePopupurl,
      data: data
    });
  }
  /***************
  *兑吧地址
  ****************/
  async getDuiBa(data) {
    return await utils.request({
      url: ServiceConfig.interface.duiBaUrl,
      data: data,
      callback: "duibaCallback"
    });
  }
  // duiBaUrl
  /***************
   *分享配置查询
   ****************/
  async queryGameShareInfo(data) {
    return await utils.request({
      url: ServiceConfig.interface.queryGameShareInfo,
      data: data || {},
      type: 'POST',
    }); saveGameShareInfo
  }

  /***************
   *分享配置保存
   ****************/
  async saveGameShareInfo(data) {
    return await utils.request({
      url: ServiceConfig.interface.saveGameShareInfo,
      data: data || {},
      type: 'POST',
    });
  } requestUpload

  /***************
   *上传图片
   ****************/
  async uploadImage(data) {
    return await utils.requestUpload({
      url: ServiceConfig.interface.uploadImage,
      data: data || {},
      type: 'POST',
      dataType: 'json',
    });
  }


}


const service = new Service();
export { service }