
class ServiceConfig {
  static domain() {
    let config = null;
    switch (window.$.base.getEnvName()) {
      case 'PRE':
        config = {
          actDomain: '//gamepre.cnsuning.com',
          PASSPORT_HOST: 'https://impassportpre.cnsuning.com/ids/login',
          M_PASSPORT_HOST: "https://mpassportpre.cnsuning.com/ids",
          PASSPORT_HOSTS: '//impassportpre.cnsuning.com',
          vcDomain: 'http://vcspre.cnsuning.com/vcs'
        }
        break;
      case 'XGPRE':
        config = {
          actDomain: '//gameprexg.cnsuning.com',
          base: 'http://gameprexg.cnsuning.com/sngame-work-web/',
          reAuth: 'http://gameprexg.cnsuning.com/sngame-work-web/login/login.do',
          PASSPORT_HOST: 'https://impassportprexg.cnsuning.com/ids/login',
          M_PASSPORT_HOST: "https://mpassportxgpre.cnsuning.com/ids",
          PASSPORT_HOSTS: '//impassportprexg.cnsuning.com',
          vcDomain: '//vcsprexg.cnsuning.com/vcs'
        }
        break;
      case 'SIT':
        config = {
          actDomain: '//gamesit.cnsuning.com',
          base: 'https://mysit.cnsuning.com/',
          PASSPORT_HOST: 'https://impassportsit.cnsuning.com/ids/login',
          M_PASSPORT_HOST: "https://mpassportsit.cnsuning.com/ids",
          PASSPORT_HOSTS: '//impassportsit.cnsuning.com',
        }
        break;
      case 'PRD':
        config = {
          actDomain: '//game.suning.com',
          base: 'https://loginst.suning.com/',
          reAuth: 'http://game.suning.com/sngame-work-web/login/login.do',
          PASSPORT_HOST: 'https://impassport.suning.com/ids/login',
          M_PASSPORT_HOST: "https://mpassport.suning.com/ids",
          PASSPORT_HOSTS: '//impassport.suning.com',
          vcDomain: '//vcs.suning.com/vcs'
        }
        break;
      case 'DEV':
        config = {
          actDomain: '//gameprexg.cnsuning.com',
          base: 'http://gameprexg.cnsuning.com/sngame-work-web/',
          reAuth: 'http://gameprexg.cnsuning.com/sngame-work-web/login/login.do',
          PASSPORT_HOST: 'https://impassportprexg.cnsuning.com/ids/login',
          M_PASSPORT_HOST: "https://mpassportxgpre.cnsuning.com/ids",
          PASSPORT_HOSTS: '//impassportprexg.cnsuning.com',
          vcDomain: '//vcsprexg.cnsuning.com/vcs'
        }
        break;
      default:
        config = {}
    }
    return config;
  }
  static interface = {
    // 查询运营系统左侧菜单
    queryAccountMenu: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/menu/queryAccountMenu.do',
    // 查询所有游戏分类(层级)
    queryAllGameType: ServiceConfig.domain().actDomain + '/sngame-work-web/public/gamePlaza/queryAllGameType.do',
    //根据分类、分页参数、游戏名称查询游戏
    queryGameList: ServiceConfig.domain().actDomain + '/sngame-work-web/public/gamePlaza/queryGameList.do',
    // 我的活动列表
    queryActivityList: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/activityMng/queryActivityList.do',
    //删除活动
    delActivity: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/activityMng/delActivity.do',
    //登录
    passporturl: ServiceConfig.domain().PASSPORT_HOST,
    //首页创建弹窗
    esPopupurl: ServiceConfig.domain().actDomain + '/sngame-work-web/public/gamePlaza/queryGameDetail.do',
    //获取不同类型兑吧免登地址
    duiBaUrl: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/duiba/getTokenLoginUrl.do',
    //获取草稿活动code
    draftMove: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/activityMng/getDraftActivityCode.do',
    //首页预览弹窗
    prePopupurl: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/activityMng/queryActivityInfo.do',
    // 分享配置查询
    queryGameShareInfo: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/gameShare/queryGameShareInfo.do',
    // 分享保存
    saveGameShareInfo: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/gameShare/saveGameShareInfo.do',
    // 图片上传
    uploadImage: ServiceConfig.domain().actDomain + '/sngame-work-web/upload/uploadImage.do',
    // 奖项列表查询 
    awardList: ServiceConfig.domain().actDomain + '/sngame-work-web/giftPackage/list.do',
    // 奖项单个查询
    awardQueryByPK: ServiceConfig.domain().actDomain + '/sngame-work-web/giftPackage/queryByPK.do',
    // 奖项修改
    awardUpdate: ServiceConfig.domain().actDomain + '/sngame-work-web/giftPackage/update.do',
    // 奖项新增
    awardAdd: ServiceConfig.domain().actDomain + '/sngame-work-web/giftPackage/add.do',

    // 奖项删除
    awardDel: ServiceConfig.domain().actDomain + '/sngame-work-web/giftPackage/del.do',
    // 礼品列表查询 
    prizeList: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/list.do',
    // 礼品单条查询 
    prizeQueryByPK: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/queryByPK.do',
    // 礼品修改
    prizeUpdate: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/update.do',
    // 礼品新增 
    prizeAdd: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/add.do',
    // 礼品删除
    prizeDel: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/del.do',
    // 查询礼品品类型动态参数字段 
    queryPTDynamicParam: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/queryPTDynamicParam.do',
    // 优惠券信息查询
    queryCoupon: ServiceConfig.domain().actDomain + '/sngame-work-web/prize/queryCoupon.do',
    // 根据游戏活动id查活动id 
    getDraftActivityCode: ServiceConfig.domain().actDomain + '/sngame-work-web/gateway/activityMng/getDraftActivityCode.do',

    //新建礼包
    createAward: ServiceConfig.domain().actDomain + '/sngame-work-web/giftPackage/add.do',
    // 登陆后重新写入inAuthId
    reAuth: ServiceConfig.domain().reAuth
  }
}
export { ServiceConfig }


