import React, {Component} from 'react';
import './index.less';
import {Tabs, Button, Form, Checkbox, Radio, Input, Modal, Icon, message} from 'antd';
import GiftTab from '../GiftTab';
import FormJiangXiangChild from '../formJiangXiangChild'
import {awardService} from '../../../service/awardService';

const {TabPane} = Tabs;
const FormItem = Form.Item;

class Award extends Component {
    constructor(props) {
        super(props);
        const panes = [];
        const activityCode = ''
        this.newTabIndex = panes.length + 1;
        this.state = {
            activeKey: '',
            panes,
            activityCode,
            modalVisible: false,
            titleList: [],
        };
    }
    async componentDidMount(){
        console.log('传过来的activityCode',this.props.activityCode)
        let temporaryData = [];

        // let getDraftActivityCode  = await awardService.getDraftActivityCode({gameId:'GM0000000011'})
        // console.log(getDraftActivityCode,'iddd')
        let awardList = await awardService.awardList({
            pageNumber:1,
            pageSize: 10,
            activityCode: this.props.activityCode
        })
        // this.state.activityCode = getDraftActivityCode.data.result;
        if(awardList.data && awardList.data.result && awardList.data.result.totalDataCount>0) {
            let singlePackData = await awardService.awardQueryByPK({packCode: awardList.data.result.datas[0].packCode})
            console.log(singlePackData, '查询')
            if (singlePackData.data.resultCode == '0000') {
                temporaryData.push(...awardList.data.result.datas)
            }
            const {panes,titleList} = this.state;
            temporaryData.map((item, index) => {
                titleList.push({
                    title: item.packName,
                    key: item.packCode,
                });
                item.key = item.packCode;
                item.title = item.packName
            })
            panes.push(...temporaryData)
            panes[0].key = temporaryData[0].packCode;
            panes[0].prizeInfoList = singlePackData.data.result.prizeInfoList
            panes[0].rewardRule = singlePackData.data.result.rewardRule

            let activeKey = temporaryData[0].packCode;
            this.setState({panes, activeKey});
        }
    }

    async onChange(activeKey){
        let singlePackData = await awardService.awardQueryByPK({packCode: activeKey})
        const {panes} = this.state;
        panes.map((item,index)=>{
            if(item.packCode==singlePackData.data.result.packCode){
                Object.assign(item,singlePackData.data.result)
            }
        })
        this.setState({panes, activeKey});

    };

    onEdit = (targetKey, action) => {
        console.log(action)
        this[action](targetKey);
    };

    async add(){
        const {panes,titleList} = this.state;
        let newTabKey = panes.length;
         newTabKey += 1;
        const title = `奖项${newTabKey}`;
        console.log(title,'title')
        let data ={
            giftPackDTO:JSON.stringify({
                activityCode: this.props.activityCode,
                packName: title,
                grantType: 1
            }),
        };

        console.log(awardService,'awardService')

        let awardAdd = await awardService.awardAdd(data);
        console.log(awardAdd,'添加接口返回的')
        let packCode = ''
        if(awardAdd.data&&awardAdd.data.resultCode=='0000'){
            packCode = awardAdd.data.result;
            this.state.packCode = packCode;
            console.log(awardAdd,'新建奖项')
            let activeKey = packCode

            // title单独储存修改
            titleList.push({
                title: title,
                key: packCode,
            });
            console.log('titleList',titleList)
            panes.push({
                title,
                packCode:packCode,
                key: activeKey,
                grantType: 1,
                packName: title,
                total: '',
                dailyLimitUser: "",
                timeLimitUser: ""
            });

            this.setState({panes, activeKey});
        }
        
    };

    async remove(targetKey) {
        let {activeKey} = this.state;
        targetKey = activeKey;
        // 确定删除的项 对active新赋值
        let lastIndex;
        this.state.panes.forEach((pane, i) => {
            if (pane.key === targetKey) {
                lastIndex = i - 1;
            }
        });
        const panes = this.state.panes.filter(pane => pane.key !== targetKey);
        if (panes.length && activeKey === targetKey) {
            if (lastIndex >= 0) {
                activeKey = panes[lastIndex].key;
            } else {
                activeKey = panes[0].key;
            }
        }
        // title同步删除
        const titleList = this.state.titleList.filter(item => item.key !== targetKey);

        let packCode = this.state.packCode
        //删除
        let awardDel = await awardService.awardDel({packCode: targetKey});
        if(awardDel.data&&awardDel.data.resultCode==='0000'){
            message.success('删除成功');
            this.setState({
                panes,
                activeKey,
                titleList,
                modalVisible: false,
            });
        } else {
            message.error('网络开小差了，请稍后再试~');
            this.setState({
                modalVisible: false,
            });
        }
        
    };

    // 同步子组件的title
    changeTabTitle (data) {
        console.log('奖项changeTabTitle',data)
        if (data.title && data.title.length <= 8) {
            let newTitleList = this.state.titleList.map((item,index) => {
                if (item.key == data.key) {
                    return data
                }
                return item
            })
            console.log('newTitleList',newTitleList)
    
            this.setState({
                titleList: newTitleList
            })
        }
    }

    // 打开对话框
    showModal () {
        this.setState({
            modalVisible: true,
        })
    }
    // 确定的回调
    modalOk () {
        this.remove();
    }
    // 取消的回调
    modalCancel () {
        this.setState({
            modalVisible: false,
        })
    }


    render() {
        const {getFieldDecorator} = this.props.form;
        const formLayout = {
            labelCol: {span: 9},
            wrapperCol: {span: 12},
        };
        const formItemLayout = {
            labelCol: {span: 4},
            wrapperCol: {span: 10},
        };
        const { panes, titleList } = this.state;

        console.log(this.state.panes,'要渲染的数据')
        return (
            <div className="Award">
                <div className="award-title">
                    <p className="title-txt">请设置活动需要派发的奖项</p>
                    <div className="icon-add" onClick={this.add.bind(this)}></div>
                    <div className="icon-subtract" onClick={this.showModal.bind(this)}></div>
                    <Modal
                        centered
                        className="modal-min-del"
                        visible={this.state.modalVisible}
                        onOk={this.modalOk.bind(this)}
                        onCancel={this.modalCancel.bind(this)}
                    >
                        <p className="delete-txt">
                            <Icon className="icon-ques" type="question-circle" />
                            确定删除此奖项配置？
                        </p>
                    </Modal>
                </div>

                <Tabs
                    hideAdd
                    onChange={this.onChange.bind(this)}
                    activeKey={this.state.activeKey}
                    type="editable-card"
                    onEdit={this.onEdit}
                    className="award-tab"
                >
                    {panes.map((item,index) => (
                        <TabPane tab={titleList[index].title} key={item.key}>
                            {/* 奖项form */}
                            <FormJiangXiangChild 
                                key={item.key}
                                UUID = {item.packCode}
                                awardName={item.title}
                                data={item}
                                changeTabTitle={this.changeTabTitle.bind(this)}
                            />
                            {/* 礼品form */}
                            <GiftTab UUID = {item.packCode} data={item.prizeInfoList}/>
                        </TabPane>
                    ))}
                </Tabs>
            </div>
        );
    }
}

export default Form.create()(Award);