import React, { Component } from 'react';
import HomeHeader from './Header';
import NavLeft from './NavLeft';
import './index.less';
import { Layout } from 'antd';

const { Content, Sider } = Layout;

class Home extends Component {

  render() {
    return (
      <div className="Home">
        <HomeHeader />
        <Layout className="content">
          <Sider width={180}>
            <NavLeft />
          </Sider>
          <Content>
            {this.props.children}
          </Content>
        </Layout>
      </div>
    );
  }
}

export default Home;