class EventDispatcher {
  constructor() {
    this.functions = {};
  }
  addEventListener(type, fun, target) {
    if (!this.functions[type]) {
      this.functions[type] = [];
    }
    let funarr = this.functions[type];
    target = target || null;
    funarr.push(
      { functionname: fun, functionexcuter: target }
    )
  }
  removeEventListener(type, fun, target) {
    const funarr = this.functions[type];
    if (!funarr) return;
    for (var i = 0; i < funarr.length; i++) {
      let _fun = funarr[i]['functionname'];
      let excuter = funarr[i]['functionexcuter'];
      if (_fun === fun && target === excuter) {
        funarr.splice(i, 1);
        return;
      }
    }
  }
  dispatchEvent(event) {
    let type = event.type;
    event.target=this;
    let funs = this.functions[type];
    if (!funs) return;
    for (var i = 0; i < funs.length; i++) {
      const fun = funs[i]['functionname'];
      const excuter = funs[i]['functionexcuter'];
      try {
        if (excuter) {
         
          fun.apply(excuter, [event]);
        }
        else {
          fun(event);
        }
       
      }
      catch (e) {
        throw e;
      }
    }
  }
}
export default EventDispatcher;
export { EventDispatcher }
