import React, {Component} from 'react';
import './index.less';
import {Tabs, Button, Form, Checkbox, Radio, Input} from 'antd';
import GiftTab from '../GiftTab';
import {awardService} from '../../../service/awardService';

const {TabPane} = Tabs;
const FormItem = Form.Item;

class formJiangXiangChild extends Component {
    constructor(props) {
        super(props);
        const panes = [];
        // this.newTabIndex = panes.length + 1;
        this.state = {
            activeKey: '',
            panes,
            UUID: this.props.UUID,
            awardName: this.props.awardName,
            item: this.props.item
        };
    }

    componentWillMount() {

    }

    componentWillReceiveProps(nextProps) {
        // debugger;
        // this.props.form.setFieldsValue(this.state.data)
    }

    onChange = activeKey => {
        this.setState({activeKey});
    };

    async clickFunEdit(e) {
        // 修改form内radio 未自动同步的bug
        if(e.target.type === "radio" && e.target.name === 'radio-group') {
            this.props.form.setFieldsValue({
                'radio-group': e.target.value
            })
        }

        this.props.form.validateFields((err, values) => {
            if (!err) {
                let childData = this.props.form.getFieldsValue();
                childData.packCode = '' + this.state.UUID;
                childData['grantType'] = childData['radio-group'];
                childData.rewardRule = {}
                
                if (childData.dailyLimitUser) {
                    childData.rewardRule.dailyLimitUser = Number(childData.dailyLimitUser);
                }
                if (childData.timeLimitUser) {
                    childData.rewardRule.timeLimitUser = Number(childData.timeLimitUser);
                }

                // 空字符串入参需删除
                childData.total || delete childData['total']
                delete childData['radio-group']
                delete childData['dailyLimitUser']
                delete childData['timeLimitUser']
                let data = {
                    giftPackDTO: JSON.stringify(childData),
                };
                let awardUpdate = awardService.awardUpdate(data)

            } else {

            }
        });

    };

    handleTitle (e) {
        e.persist();
        console.log('奖项target title',e.target.value)
        let data = {
            title: e.target.value,
            key: this.props.UUID
        }
        this.props.changeTabTitle(data);
    }


    render() {
        const {getFieldDecorator} = this.props.form;
        const formLayout = {
            labelCol: {span: 9},
            wrapperCol: {span: 12},
        };
        const formItemLayout = {
            labelCol: {span: 4},
            wrapperCol: {span: 10},
        };
        // debugger;
        console.log(this.props.data, '闯过来的')
        let currData = this.props.data;
        return (
            <Form {...formLayout} colon={false} labelAlign='left'>
                <div className="pack-code">
                    <span className="pack-code-txt">奖项编码</span>
                    <span className="pack-code-num">{this.state.UUID}</span>
                </div>
                {/* 奖项发放设置 */}
                <div className="pack-set">
                    <div className="set-name">
                        奖项发放设置
                        <div className="dashed-border"></div>
                    </div>
                    <FormItem {...formItemLayout} label="发放方式">
                        {getFieldDecorator('radio-group', {
                            rules: [{
                                required: true,
                                message: '请选择发放方式'
                            }],
                            initialValue: currData.grantType,
                        })(
                            <Radio.Group name="radio-group" onChange={this.clickFunEdit.bind(this)}>
                                <Radio value={1}>全部</Radio>
                                <Radio value={2}>顺序</Radio>
                                <Radio value={3}>随机</Radio>
                            </Radio.Group>
                        )}
                    </FormItem>
                    <div className="row-two">
                        <FormItem label="奖项数量">
                            {getFieldDecorator('total', {
                                rules: [{
                                    required: false,
                                    message: '请输入奖项数量'
                                }],
                                getValueFromEvent: (event) => {
                                    return event.target.value.replace(/[^\d]/g, '').substring(0,8);
                                },
                                initialValue: currData.total
                            })(<Input onBlur={this.clickFunEdit.bind(this)}/>)}
                        </FormItem>
                        <FormItem label="奖项展示名称">
                            {getFieldDecorator('packName', {
                                rules: [{
                                    required: true,
                                    message: '请输入奖项名称'
                                }],
                                getValueFromEvent: (event) => {
                                    return event.target.value.substring(0,8);
                                },
                                initialValue: currData.packName
                            })(<Input onBlur={this.clickFunEdit.bind(this)} onChange={this.handleTitle.bind(this)}/>)}
                        </FormItem>
                    </div>
                    <div className="row-two">
                        <FormItem label="每人每天限领数量">
                            {getFieldDecorator('dailyLimitUser', {
                                rules: [{
                                    required: false,
                                    message: '请输入每人每天限领数量'
                                }],
                                getValueFromEvent: (event) => {
                                    return event.target.value.replace(/[^\d^\-]/g, '').substring(0,8);
                                },
                                initialValue: currData.rewardRule?currData.rewardRule.dailyLimitUser:''
                            })(<Input placeholder="" onBlur={this.clickFunEdit.bind(this)}/>)}
                        </FormItem>
                        <FormItem label="活动期间限领数量">
                            {getFieldDecorator('timeLimitUser', {
                                rules: [{
                                    required: false,
                                    message: '请输入活动期间限领数量'
                                }],
                                getValueFromEvent: (event) => {
                                    return event.target.value.replace(/[^\d^\-]/g, '').substring(0,8);
                                },
                                initialValue: currData.rewardRule?currData.rewardRule.timeLimitUser:''
                            })(<Input placeholder="" onBlur={this.clickFunEdit.bind(this)}/>)}
                        </FormItem>
                    </div>
                </div>
            </Form>

        );
    }
}

export default Form.create()(formJiangXiangChild);