import React, { Component } from 'react';
import './index.less';
import { Tabs, Button, Icon, Form, Input, Upload, Switch, Checkbox, message } from 'antd';
import { service } from '../../../service/service';
// import { awardService } from '../../../service/awardService';

const { TabPane } = Tabs;
const FormItem = Form.Item;

function onValuesChange(props,changedValues,allValues) {
    console.log('allValues:',allValues)
    console.log('changedValues:',changedValues)
    console.log('props:',props)
}

class Share extends Component {

  state = {
    id: '', // 主键id
    templateId: '', // 模板id
    activityCode: '', // 活动编码
    linkTitle: '', // 链接分享标题
    linkDesc: '', // 链接分享文案
    linkIconUrl: '', // 链接分享图标URL
    weChatPicX: '360', // 小程序分享底图二维码x轴 暂时写死
    weChatPicY: '630', // 小程序分享底图二维码y轴 暂时写死
    longPicUrl: '', // 客户端长图URL
    longPicX: '489', // 客户端长图二维码x轴  暂时写死
    longPicY: '587', // 客户端长图二维码y轴  暂时写死
    weChatTitle: '', // 小程序分享标题
    weChatCardUrl: '', // 小程序分享卡片URL
    weChatPicUrl: '', // 小程序分享底图URL
    weChatShareSwitch: '0',// 小程序分享开关
    longPicShareSwitch: '0',// 客户端长图分享
    longPicRelationSwitch: '0'// 长图关联小程序开关 
  }

  componentWillMount() {
    this.queryGameShareInfo({
      activityCode: this.props.activityCode,
    })
  }

  async queryGameShareInfo(data) {
    let res = await service.queryGameShareInfo(data);
    if (res.data && res.data.resultCode === 'GMSH01') {
      let data = res.data.result;
      this.setState(data)
    }
  }
  // 分享保存
  async saveGameShareInfo(data) {
    let res = await service.saveGameShareInfo(data);
    if(res.data && res.data.resultCode === "GMSA01") {
      message.success('保存成功！');
    }
  }  

  loginSubmit = (e)=> {
    e && e.preventDefault();
    // const _this = this;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let formValue = this.props.form.getFieldsValue();
        console.log(formValue,'123');
        let obj = {
          activityCode: this.props.activityCode,
          weChatPicX: '360',
          weChatPicY: '630',
          longPicX: '489',
          longPicY: '587',
          weChatShareSwitch: formValue.weChatShareSwitch ? '1' : '0',
          longPicShareSwitch: formValue.longPicShareSwitch ? '1' : '0',
          longPicRelationSwitch: formValue.longPicRelationSwitch ? '1' : '0',
        };
        let imgUrls = {
          linkIconUrl: this.state.linkIconUrl,
          weChatPicUrl: this.state.weChatPicUrl,
          longPicUrl: this.state.longPicUrl,
          weChatCardUrl: this.state.weChatCardUrl,
        }
        let saveData = Object.assign({},formValue,imgUrls,obj);
        console.log(saveData,'savedata')
        // 调保存接口
        this.saveGameShareInfo(saveData)
      } else {
        message.error('存在未填的必填项!');
      }
    });
  };
  
  normFile (e) {
    console.log('Upload event:', e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };
  
   beforeUpload = (file) => {
    // 取消 Upload 组件的默认上传
    return false; 
  }

  // 上传图片
  uploadImage = (file, urlName, limitSize) => {
    // 上传前验证
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
      message.error('文件格式错误,请重新上传!');
      return false;
    }
    const LimitSize = limitSize || 200;
    const isLimitSize = file.size / 1024 < LimitSize;
    if (!isLimitSize) {
      message.error('文件大小超过限制,请重新上传!');
      return false;
    }
    // form提交
    const formData = new FormData();
    formData.append('uploadFile', file);
    service.uploadImage(formData).then(res => {
      if(res.data && res.data.resultCode === '0' && res.data.result) {
        let imgUrl = res.data.result;
        let obj = {};
        obj[urlName] = imgUrl
        this.setState(obj)
        message.success('图片上传成功!');
      } else {
        message.error('图片上传失败,请重新上传!');
      }
    }).catch(err => {
      message.error('图片上传失败,请重新上传!');
    })
  }

  // 小程序分享卡片图
  weChatCardUpload = info => {
    console.log('上传info',info)
    this.uploadImage (info.file, 'weChatCardUrl', 100)
  };
  // 小程序分享图片图
  weChatPicUpload = info => {
    this.uploadImage (info.file, 'weChatPicUrl', 200)
  };
  // 易购app分享长图
  longPicUpload = info => {
    this.uploadImage (info.file, 'longPicUrl', 200)
  };
  // 链接分享图片
  linkIconUpload = info => {
    this.uploadImage (info.file, 'linkIconUrl', 20)
  };

  // 关联分享开关打开  小程序分享必填
  longPicRelationChange = (e) => {
    if (e.target.checked == true && this.props.form) {
      this.props.form.setFieldsValue({
        weChatShareSwitch: true
      })
    } 
  }
  // 易购app分享开关关闭  关联分享开关关闭
  longPicShareChange = e => {
    console.log(e)
    if (e === false) {
      this.props.form.setFieldsValue({
        longPicRelationSwitch: false
      })
    }
  }
  
  render() {
    const formItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 4 },
    };

    const formItemLayout2 = {
      labelCol: { span: 4 },
      wrapperCol: { span: 6 },
    };
    const LayoutShare = {
      labelCol: { span: 4 },
      wrapperCol: { span: 15 },
    };
    const { getFieldDecorator } = this.props.form;
    let formValue = this.props.form.getFieldsValue();
    
    // 上传图片相关
    const uploadButton = (
      <div>
        <div className="icon-imgupload"></div>
        <div className="ant-upload-text">选择图片</div>
      </div>
    );
    
    const { linkIconUrl, weChatPicUrl, longPicUrl, weChatCardUrl, weChatTitle, linkTitle ,linkDesc } = this.state;

    return (
      <div className="Share">
        <div className="share-title">
          <p className="title-txt">分享设置</p>
          {/* <Button type="primary" onClick={this.loginSubmit} className="shareForm-btn">
              分享保存
          </Button> */}
        </div>

        <Form colon={false} labelAlign='left'>
          <Tabs type="card" className="share-tab">

            {/* 小程序分享tab */}
            <TabPane tab="小程序分享" key="1">
              <div className="miniPro-pic">
                <div className="share-switch">
                  <FormItem {...formItemLayout} label="分享开关">
                    {getFieldDecorator('weChatShareSwitch', { 
                      valuePropName: 'checked',
                      initialValue: this.state.weChatShareSwitch === '1',
                    })(<Switch disabled={formValue.longPicRelationSwitch} />)}
                  </FormItem>
                </div>
                <div className="miniPro-shareCard">
                  <div className="set-name">
                    小程序分享卡片<div className="dashed-border"></div>
                  </div>
                  <div className="miniPro-preview">
                    <p>效果预览</p>
                    <div className="miniPro-pre-img">
                      <div className="container">
                        <div className="con-top">
                          <div className="sn-logo"></div>苏宁易购
                        </div>
                        <div className="title">{formValue.weChatTitle || weChatTitle || '帮我点一下，水果好礼免费拿！'}</div>
                        { this.state.weChatCardUrl ? (<img className="pre-img" src={this.state.weChatCardUrl} alt='' />) : (<div className="def-pre-img"></div>) }
                        <div className="con-btm">
                          <div className="icon-miniPro"></div>小程序
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="upload-img">
                    <FormItem label="分享文案">
                      {getFieldDecorator('weChatTitle',{
                        initialValue: this.state.weChatTitle,
                        rules:[
                          {
                            required: formValue.weChatShareSwitch,
                            message: '请输入分享文案'
                          }
                        ]
                      })(<Input placeholder="分享文案名称展示" />)}
                    </FormItem>
                    <FormItem label="上传图片">
                      {getFieldDecorator('weChatCardUrl', {
                        valuePropName: 'fileList',
                        getValueFromEvent: this.normFile,
                        initialValue: this.state.weChatCardUrl,
                        rules:[
                          {
                            required: this.state.weChatCardUrl ? false : formValue.weChatShareSwitch,
                            message: '请上传图片'
                          }
                        ]
                      })(
                        <Upload
                          accept=".png, .jpg, .jpeg"
                          listType="picture-card"
                          className="img-uploader"
                          showUploadList={false}
                          beforeUpload={this.beforeUpload}
                          onChange={this.weChatCardUpload}
                        >
                        { weChatCardUrl ? 
                          (<div>
                              <img src={weChatCardUrl} alt="" style={{ width: '100%' }} />
                              <div className="img-choose">重新上传</div>
                          </div>)
                            : uploadButton }
                        </Upload>
                      )}
                    </FormItem>
                    <div className="upload-desc">图片尺寸 500x400，文件格式 PNG、JPG，文件大小不超过100KB</div>
                  </div>
                </div>

                {/* 小程序分享图片设置 */}
                <div className="miniPro-shareImg">
                    <div className="set-name">
                      小程序分享图片设置<div className="dashed-border"></div>
                    </div>
                    <div className="miniPro-preview">
                      <p>效果预览</p>
                      <div className="miniPro-pre-img">
                        { this.state.weChatPicUrl ? (<img src={this.state.weChatPicUrl} alt='' />) : (<div className="def-pre-img"></div>)}
                      </div>
                    </div>
                    <div className="upload-img">
                      <FormItem label="上传图片">
                        {getFieldDecorator('weChatPicUrl', {
                          valuePropName: 'fileList',
                          getValueFromEvent: this.normFile,
                          initialValue: this.state.weChatPicUrl,
                          rules:[
                            {
                              required: this.state.weChatPicUrl ? false : formValue.weChatShareSwitch,
                              message: '请上传图片'
                            }
                          ]
                        })(
                          <Upload
                            accept=".png, .jpg, .jpeg"
                            listType="picture-card"
                            className="img-uploader"
                            showUploadList={false}
                            beforeUpload={this.beforeUpload}
                            onChange={this.weChatPicUpload}
                          >
                          { weChatPicUrl ? 
                            (<div>
                                <img src={weChatPicUrl} alt="" style={{ width: '100%' }} />
                                <div className="img-choose">重新上传</div>
                            </div>)
                              : uploadButton }
                          </Upload>
                        )}
                      </FormItem>
                      <div className="upload-desc">图片尺寸 600x900，文件格式 PNG、JPG，文件大小不超过200KB</div>
                    </div>
                  </div>
              </div>
            </TabPane>

            {/* 易购APP分享tab */}
            <TabPane tab="易购APP" key="2">
              <div className="long-pic">
                <div className="share-switch">
                  <FormItem {...formItemLayout} label="分享开关">
                    {getFieldDecorator('longPicShareSwitch', { 
                      valuePropName: 'checked',
                      initialValue: this.state.longPicShareSwitch === '1',
                    })(<Switch onChange={this.longPicShareChange} />)}
                  </FormItem>
                  <FormItem {...formItemLayout2} label="小程序分享设置">
                    {getFieldDecorator('longPicRelationSwitch', { 
                      valuePropName: 'checked',
                      initialValue: this.state.longPicRelationSwitch === '1',
                    })(<Checkbox onChange={this.longPicRelationChange} disabled={!formValue.longPicShareSwitch}>关联小程序分享设置</Checkbox>)}
                  </FormItem>
                  <p className="relSwitch-desc">从易购APP分享到小程序的设置，勾选“关联小程序分享设置”为打开，且需在小程序分享TAB进行设置</p>
                </div>
                <div className="long-preview">
                  <p>效果预览</p>
                  <div className="long-pre-img">
                  { this.state.longPicUrl ? (<img src={this.state.longPicUrl} alt='' />) : (<div className="def-long-img"></div>)}
                  </div>
                </div>
                <div className="upload-img">
                  <FormItem label="上传图片">
                  {getFieldDecorator('longPicUrl', {
                    valuePropName: 'fileList',
                    getValueFromEvent: this.normFile,
                    initialValue: this.state.longPicUrl,
                    rules:[
                      {
                        required: this.state.longPicUrl ? false : formValue.longPicShareSwitch,
                        message: '请上传图片'
                      }
                    ]
                  })(
                    <Upload
                      accept=".png, .jpg, .jpeg"
                      listType="picture-card"
                      className="img-uploader"
                      showUploadList={false}
                      beforeUpload={this.beforeUpload}
                      onChange={this.longPicUpload}
                    >
                    { longPicUrl ? 
                      (<div>
                          <img src={longPicUrl} alt="" style={{ width: '100%' }} />
                          <div className="img-choose">重新上传</div>
                      </div>)
                        : uploadButton }
                    </Upload>
                  )}
                </FormItem>
                <div className="upload-desc">图片尺寸 750x1158，文件格式 PNG、JPG，文件大小不超过200KB</div>
                </div>
              </div>
            </TabPane>

            {/* H5分享tab */}
            <TabPane tab="H5分享" key="3">
              <div className="link-preview">
                <p>效果预览</p>
                <div className="pre-img">
                  <div className="pre-avatar"></div>
                  <div className="inner clearfix">
                    <div className="title">{formValue.linkTitle || linkTitle || '吃小鱼赢4999免单'}</div>
                    <div className="pre-con clearfix">
                      <div className="con-txt">{formValue.linkDesc|| linkDesc || '我的排名已经超过你了,一起来玩瓜分云钻~'}</div>
                      { this.state.linkIconUrl ? (<img className="con-img" src={this.state.linkIconUrl} alt='' />) : (<div className="def-link-img"></div>)}
                    </div>
                  </div>
                </div>
              </div>
              <div className="act-name">
                {/* <span className="name-title">活动标题<b>*</b></span> */}
                <FormItem {...LayoutShare} label="活动标题">
                {getFieldDecorator('linkTitle',{
                  initialValue: this.state.linkTitle,
                  rules:[
                    {
                      required: true,
                      message: '请输入活动标题'
                    }
                  ]
                })(<Input placeholder="活动标题名称展示" />)}
                </FormItem>
              </div>
              <div className="act-name">
                {/* <span className="name-title">分享文案<b>*</b></span> */}
                <FormItem {...LayoutShare} label="分享文案">
                {getFieldDecorator('linkDesc',{
                  initialValue: this.state.linkDesc,
                  rules:[
                    {
                      required: true,
                      message: '请输入分享文案'
                    }
                  ]
                })(<Input placeholder="活动分享文案展示" />)}
                </FormItem>
              </div>
              <div className="upload-img">
                <FormItem label="上传图片">
                  {getFieldDecorator('linkIconUrl', {
                    valuePropName: 'fileList',
                    getValueFromEvent: this.normFile,
                    initialValue: this.state.longPicUrl,
                    rules:[
                      {
                        required: !this.state.linkIconUrl,
                        message: '请上传图片'
                      }
                    ]
                  })(
                    <Upload
                      accept=".png, .jpg, .jpeg"
                      listType="picture-card"
                      className="img-uploader"
                      showUploadList={false}
                      beforeUpload={this.beforeUpload}
                      onChange={this.linkIconUpload}
                    >
                    { linkIconUrl ? 
                      (<div>
                          <img src={linkIconUrl} alt="" style={{ width: '100%' }} />
                          <div className="img-choose">重新上传</div>
                      </div>)
                        : uploadButton }
                    </Upload>
                  )}
                </FormItem>
                <div className="upload-desc">图片尺寸 120x120，文件格式 PNG、JPG，文件大小不超过20KB</div>
              </div>
            </TabPane>

          </Tabs>
        </Form>
      </div>
    );
  }
}

const ShareForm = Form.create()(Share);

export default ShareForm;