import React, {Component} from 'react';
import './index.less';
import {Tabs, Button, Form, Checkbox, Radio, Input, Select, Icon, Modal, message} from 'antd';
import {awardService} from '../../../service/awardService';
import FormChild from '../formLibaoChild'

const {TabPane} = Tabs;
const {Option} = Select;
const FormItem = Form.Item;

class GiftTab extends Component {
    constructor(props) {
        super(props);

        const panes = [];
        // this.newTabIndex = panes.length + 1;
        this.state = {
            activeKey: '',
            panes,
            packCode: this.props.UUID,
            modalVisible: false,
            newTabIndex: 0,
            titleList: [],
        };
        let fakeData = this.props.data;
        console.log(fakeData,'下层')
        if(fakeData) {
            fakeData.map((item, index) => {
                this.state.titleList.push({
                    title: item.prizeName,
                    key: String(item.prizeCode),
                });
                item.title = item.prizeName;
                item.key = String(item.prizeCode)
            })
            // if (fakeData) {
                this.state.panes = this.props.data;
            // }
            this.state.activeKey = this.state.panes[0].key
        }
    }

    onChange = activeKey => {
        console.log(activeKey, 'activeKey')
        this.setState({activeKey});
    };

    onEdit = (targetKey, action) => {
        console.log(action)
        this[action](targetKey);
    };

    async add(datas) {
        const {panes,titleList} = this.state;
        let newTitleIndex = panes.length + 1;
        const newTitle = `礼品${newTitleIndex}`;
        let data = {
            prizeDto: JSON.stringify({
                prizeName: newTitle,
                packCode: this.props.UUID,
                prizeTypeCode: '1', //1是劵2是云钻
                dynamicParamValueDTOList: [{
                    "paramValue": null,
                    "paramCode":"couponCode",
                    "paramFrontFlag":"Y"
                }]
            }),
        };
        let prizeAdd = await awardService.prizeAdd(data);
        if(prizeAdd.data&&prizeAdd.data.result){
            let prizeCode = prizeAdd.data.result;
            const activeKey = `${prizeCode}`;
            console.log(prizeAdd, '新建礼包')
            if (prizeAdd.data.resultCode == '0000') {
                
                // title单独储存修改
                titleList.push({
                    title: newTitle,
                    key: prizeCode,
                });
                console.log('titleList',titleList)
                panes.push({
                    title: newTitle,
                    key: prizeCode,
                    packCode: this.props.UUID,
                    prizeCode: prizeCode,
                    prizeTypeCode: '1',
                    prizeName: newTitle,
                    couponCode: '',
                    // activityName: '',
                    amount: '',
                    total: '',
                    couponRulesShowMsg: '',
                    couponCount: '',
                    couponFaqi: '',
                    activitySecretKey: '',
                    startTime: '',
                    endTime: '',
                });
                this.setState({panes, activeKey});
            }
        }

    };

    clickFun(data) {
        // let childData = this.props.form.getFieldsValue();
        // this.clickFun.bind(this, this.props.form.getFieldsValue())
        // this.props.pfn(childData)//这个地方把值传递给了props的事件当中
    }

    async remove(targetKey) {
        console.log(targetKey)
        console.log(this.props.form.getFieldsValue(), '我这在删除')
        let { activeKey } = this.state;
        targetKey = activeKey;
        
        // 确定删除的项 对active新赋值
        let lastIndex;
        this.state.panes.forEach((pane, i) => {
            if (pane.key === targetKey) {
                lastIndex = i - 1;
            }
        });
        const panes = this.state.panes.filter(pane => pane.key !== targetKey);
        if (panes.length && activeKey === targetKey) {
            if (lastIndex >= 0) {
                activeKey = panes[lastIndex].key;
            } else {
                activeKey = panes[0].key;
            }
        }
        // title同步删除
        const titleList = this.state.titleList.filter(item => item.key !== targetKey);

        let prizeDel = await awardService.prizeDel({
            packCode: this.state.packCode,
            prizeCode: targetKey
        });
        if(prizeDel.data.resultCode=='0000'){
            message.success('删除成功');
            this.setState({
                panes,
                activeKey,
                titleList,
                modalVisible: false,
            });
        } else {
            message.error('网络开小差了，请稍后再试~');
            this.setState({
                modalVisible: false,
            });
        }
    };

    // 同步子组件的title
    changeTabTitle (data) {
        console.log('changeTabTitle',data)
        if (data.title && data.title.length <= 8) {
            let newTitleList = this.state.titleList.map((item,index) => {
                if (item.key == data.key) {
                    return data
                }
                return item
            })
            console.log('newTitleList',newTitleList)
    
            this.setState({
                titleList: newTitleList
            })
        }
    }

    // 打开对话框
    showModal () {
        if (this.state.panes && this.state.panes.length < 1) {
            return
        }
        this.setState({
            modalVisible: true,
        })
    }
    // 确定的回调
    modalOk () {
        this.remove();
    }
    // 取消的回调
    modalCancel () {
        this.setState({
            modalVisible: false,
        })
    }

    render() {
        const {getFieldDecorator} = this.props.form;
        const formLayout = {
            labelCol: {span: 9},
            wrapperCol: {span: 12},
        };
        const formItemLayout = {
            labelCol: {span: 12},
            wrapperCol: {span: 12},
        };
        const { panes, titleList } = this.state;

        console.log(this.props.UUID, "UUID")
        console.log(this.props.data,'传过来的')
        return (
            <div className="GiftTab">
                <div className="gift-set">
                    <div className="set-name">
                        礼品维护
                        <div className="dashed-border"></div>
                    </div>
                </div>
                <div className="icon-add" onClick={this.add.bind(this)}></div>
                <div className="icon-subtract" onClick={this.showModal.bind(this)}></div>
                <Modal
                    centered
                    className="modal-min-del"
                    visible={this.state.modalVisible}
                    onOk={this.modalOk.bind(this)}
                    onCancel={this.modalCancel.bind(this)}
                >
                    <p className="delete-txt">
                        <Icon className="icon-ques" type="question-circle" />
                        确定删除此礼品配置？
                    </p>
                </Modal>
                <Tabs
                    hideAdd
                    onChange={this.onChange}
                    activeKey={this.state.activeKey}
                    type="editable-card"
                    onEdit={this.onEdit}
                    className="gift-tab"
                >
                    {panes.map((item, index) => (
                        <TabPane tab={titleList[index].title} key={item.key}>
                            <FormChild
                                key={item.key}
                                UUID={this.props.UUID}
                                PRIZECODE={item.prizeCode}
                                data={item}
                                changeTabTitle={this.changeTabTitle.bind(this)}
                            />
                        </TabPane>

                    ))}
                </Tabs>
            </div>
        );
    }
}

export default Form.create()(GiftTab);