
import React from 'react';
import { Modal, Button, Alert } from 'antd';
import './establish-popup.less';
import PropTypes from 'prop-types';
import { createHashHistory } from 'history';
import { services } from '../../intergrade/service';
import QRCode from 'qrcode.react';
class estabPopup extends React.Component {

  state = {
    visible: true,
    gameDetail: {},
    urlType:1,
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
    this.props.handleClose();
  };
  createGame() {

    let Draftparams = {
      gameId: this.props.gameId,
      
    };

    services.service.getDraftMoveCode(Draftparams).then(res => {
  
      console.log("草稿活动code", res)
       if(res&&res.data&&res.data.result){
        // this.setState({ activityCode: res.data.result});
        const history = createHashHistory();
        history.push('/Edit?gameId=' + this.props.gameId+'&activityCode='+res.data.result+'&activityUrlType='+this.state.urlType);
       }
    }).catch((e) => {
      console.log("失败：", e)
    });

    

  }
   componentWillMount() {
     console.log("11112222224444",this.state.urlType)
    let params = {
      gameId: this.props.gameId,
      
    };
 
    services.service.getEsProp(params).then(res => {
    
      console.log("创建弹窗接口", res)
       if(res&&res.data&&res.data.result){
         this.setState({ gameDetail: res.data.result });
       }
        localStorage.setItem("creatUrl126", res.data.result.createUrl);
    
        var lastname = localStorage.getItem("creatUrl126");
          
       }).catch((e) => {
         console.log("失败：", e)
       });


    
   
  }


  render() {
    // debugger
    let detail = this.state.gameDetail
    {
      return (
        <div>
                <Modal
                  visible={this.state.visible}
                  onOk={this.handleOk}
                  onCancel={this.handleCancel}
                  footer={null}
                  width={690}

                >
                  <div className="popup-box">

                    <div className="popup-content">


                      <div className="popup-leftBox">
                        <img className="leftBox-img" src={detail.pictureUrl}/>
                         
                      </div>
                      <div className="popup-rightBox">
                        <div className="rightBox-content">
                          <div className="game-name-box"><span className="game-name-img"></span><span className="game-name">{detail.gameName}</span></div>
                          <div className="split-line"></div>
                          <div className="actExpTitle_New"><span className="actExpTitle_mark"></span><span className="actExpTitle_name">玩法介绍</span></div>
                          <div className="actExp_New">{detail.gameDesc}</div>
                          <div className="marketingTitle_New"><span className="marketing_mark"></span><span className="marketing_name">营销功能</span></div>
                          <div className="marketing_New">
                          {
                            detail.gameTagVOList && detail.gameTagVOList.map((item,index) =>{ 
                              return (
                                <div key={index} className="marketing">{item.gameTagName}</div>
                              )
                            })
                          }
                          </div>
                          <div className="createBtn" onClick={this.createGame.bind(this)}>创&nbsp;&nbsp;建</div>
                        </div>
                      </div>
                      <div id="result"></div>
                    </div>
                  </div>
                </Modal>
        </div>
      );

    }
  }
}

export default estabPopup;