class Event {
  constructor(type, data = null) {
   
    this.type = type;
    this.data = data;
  }
  set target(val){
  
    this._target = val;
  }
  get target(){
    return this._target;
  }
  set currentTarget(val){
    this._currentTarget=val;
  }
  get currentTarget(){
    return this._currentTarget;
  }
}
export {Event}
export default Event;
