import condition from './envconditiaon'
export default (target, name, despcirtion) => {

  let isuat = false;
  condition.forEach((con) => {
    // console.log(con)
    if (window.location.href.indexOf(con) >= 0) {
      isuat = true;
    }
  })
  if (isuat) {
    return despcirtion;
  } else {
    //let fun=despcirtion.value;
    despcirtion.value = function (target, name, despcirtion) {
      return despcirtion;
    }
    return despcirtion;
  }

}