import React, { Component } from 'react';
import './index.less';
import { Menu, Icon } from 'antd';
import { Link, withRouter } from "react-router-dom";
import { service } from '../../../service/service';
import iconfont from '../../../common/iconfont.js';
import { utils } from '../../../common/Utils';
const { SubMenu } = Menu;
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: iconfont
})

class NavLeft extends Component {
  rootSubmenuKeys = [];//所有SubMenu的menuId
  constructor(props) {
    super(props);
    this.state = {
      openKeys: [],//当前展开的 SubMenu 菜单项 key 数组	
      menus: []//菜单数据
    }
  }
  async componentWillMount() {
    // 获取菜单列表
    let res = await service.queryAccountMenu();
    console.log("菜单列表：", res);
    try {
      // 当前路由hash值
      const path = this.props.location.pathname.substr(1);
      let openKeys = [];
      res.data.result.map((item, index) => {
        if (item.hasOwnProperty('menuVOList') && item.menuVOList.length > 0) {
          this.rootSubmenuKeys.push(item.menuId);
          item.menuVOList.map((val, key) => {
            if (val.menuId == path) {
              openKeys.push(item.menuId)
            }
          })
        }
      })
      this.setState({
        openKeys: openKeys,
        menus: res.data.result
      })
    } catch (error) {

    }
  }
  // SubMenu改变回调，只展开点击当前的菜单，其他收起
  onOpenChange(openKeys) {
    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
    if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      this.setState({ openKeys });
    } else {
      this.setState({
        openKeys: latestOpenKey ? [latestOpenKey] : [],
      });
    }
  }
  //点击菜单项，收起subMenu（点击subMenu的子菜单不收起）
  onClick(e) {
    e.keyPath.length === 1 ? this.setState({ openKeys: [] }) : '';
  }
  render() {
    const path = this.props.location.pathname.substr(1);
    return (
      <div className="NavLeft">
        <Menu
          mode="inline"
          selectedKeys={[path]}
          openKeys={this.state.openKeys}
          onOpenChange={this.onOpenChange.bind(this)}
          theme='dark'
          onClick={this.onClick.bind(this)}
        >
          {
            this.state.menus.map((item, index) => {
              if (item.hasOwnProperty('menuVOList') && item.menuVOList.length > 0) {
                return (<SubMenu
                  key={item.menuId}
                  title={
                    <span>
                      <div className={'sub-icon icon-' + item.menuId}></div>
                      <span className="sub-txt">{item.menuName}</span>
                    </span>
                  }>
                  {
                    item.menuVOList.map((child, k) => {
                      return <Menu.Item key={child.menuId}>
                        <Link to={'/' + child.menuId}>
                          <div className={'icon icon-' + child.menuId}></div>
                          <span className="txt">{child.menuName}</span>
                        </Link>
                      </Menu.Item>
                    })
                  }
                </SubMenu>)
              } else {
                return (<Menu.Item key={item.menuId}>
                  <Link to={'/' + item.menuId}>
                    <div className={'icon icon-' + item.menuId}></div>
                    <span className="txt">{item.menuName}</span>
                  </Link>
                </Menu.Item>)
              }
            })
          }
        </Menu>
      </div>
    );
  }
}

export default withRouter(NavLeft)