import React, {Component} from 'react';
import './index.less';
import {Tabs, Button, Form, Checkbox, Radio, Input, Select, message} from 'antd';
import {awardService} from '../../../service/awardService';
import {utils} from '../../../common/Utils';

const {TabPane} = Tabs;
const {Option} = Select;
const FormItem = Form.Item;
// 申请查看优惠券地址  830写死
const couponUrl = "http://spms.cnsuning.com/spes-web-in/manage/showActivityPage.htm";

class tabFormChild extends Component {
    constructor(props) {
        super(props);

        this.state = {
            activeKey: '',
            panes: [],
            prizeCode: this.props.PRIZECODE,
            couponData: '',
            packCode: this.props.UUID,
        };
        console.log(this.state.prizeCode, '6')
    }

    componentDidMount() {
        // 进入回显查优惠券信息
        let item = this.props.data;
        if (item.dynamicParamValueDTOList && item.dynamicParamValueDTOList[0].paramValue) {
            let couponCode = item.dynamicParamValueDTOList[0].paramValue;
            awardService.queryCoupon({
                couponCode: couponCode,
                prizeTypeCode: 1
            }).then((queryCoupon) => {
                if (queryCoupon.data && queryCoupon.data.result.actInfoList.length>0) {
                    this.setState({couponData: queryCoupon.data.result.actInfoList[0]})
                }
            });
        }
    }

    onChange = activeKey => {
        this.setState({activeKey});
    };

    clickFunEdit(e) {
        e.persist();
        console.log('eventtttttttttt',e)
        if (e.target.id === 'paramValue') {
            // alert('paramValue')
        }
        //校验
        this.props.form.validateFields(async (err, values) => {
            if (!err) {
                let childData = this.props.form.getFieldsValue();
                // 如果修改了查券编码
                if (e.target.id === 'paramValue') {
                    // awardService.queryCoupon({
                    //     couponCode: childData.paramValue,
                    //     prizeTypeCode: 1
                    // }).then((queryCoupon) => {
                    //     if (queryCoupon.data && queryCoupon.data.result && queryCoupon.data.result.actInfoList.length>0) {
                    //         this.setState({couponData: queryCoupon.data.result.actInfoList[0]})
                    //         // 设置查券金额
                    //         this.props.form.setFieldsValue({
                    //             amount: queryCoupon.data.result.actInfoList[0].couponAmount
                    //         })
                    //     }
                    // });
                    let queryCoupon = await awardService.queryCoupon({
                        couponCode: childData.paramValue,
                        prizeTypeCode: 1
                    });
                    console.log('queryCouponqueryCoupon',queryCoupon)
                    if (queryCoupon.data && queryCoupon.data.result && queryCoupon.data.result.actInfoList.length>0) {
                        this.setState({couponData: queryCoupon.data.result.actInfoList[0]})
                        // 设置查券金额
                        let amount = queryCoupon.data.result.actInfoList[0].couponAmount;
                        childData.amount = amount;
                        this.props.form.setFieldsValue({amount});
                    }

                }

                // 修改礼品入参
                childData.prizeCode = this.state.prizeCode;
                childData.packCode = this.state.packCode;
                // 空字符串的入参要删除
                childData.amount || delete childData['amount'];
                childData.total || delete childData['total'];

                childData.dynamicParamValueDTOList = [{
                    "paramValue": childData.paramValue,
                    "paramCode": "couponCode",
                    "paramFrontFlag": "Y"
                }];
                delete childData['paramValue']

                let data = {
                    prizeDto: JSON.stringify(childData),
                };
                console.log('礼品修改入参',childData);
                // if (childData.prizeName == '') {
                //     message.warning('请输入奖品名')
                // }
                let prizeUpdate = awardService.prizeUpdate(data);
                

            } else {

            }
        })
    }

    handleTitle (e) {
        e.persist();
        console.log('target title',e.target.value)
        let data = {
            title: e.target.value,
            key: this.props.PRIZECODE
        }
        this.props.changeTabTitle(data);
    }

    render() {
        console.log(this.props.UUID, this.props.PRIZECODE, 22221)
        const {getFieldDecorator} = this.props.form;
        const formLayout = {
            labelCol: {span: 9},
            wrapperCol: {span: 12},
        };
        const formItemLayout = {
            labelCol: {span: 12},
            wrapperCol: {span: 12},
        };

        let couponData = this.state.couponData;
        let item = this.props.data;
        console.log(this.props.data, '闯过来的')
        return (

            <Form  {...formLayout} colon={false} labelAlign='left'>
                {/* 奖项发放设置 */}
                <div className="gift-set">
                    <div className="select-option">
                        <Form.Item {...formItemLayout} label="礼品类型" style={{width: 273,marginRight: 40}}>
                            {getFieldDecorator(`prizeTypeCode`, {
                                rules: [
                                    {required: true, message: '请选择礼品类型'},
                                ],
                                initialValue: item.prizeTypeCode
                            })(
                                <Select>
                                    <Option value="1">优惠券</Option>
                                    {/* <Option value="2">云钻</Option> */}
                                </Select>
                            )}
                        </Form.Item>
                        <a target="_blank" href={couponUrl}>申请/查看优惠券</a>
                    </div>

                    <div className="quan-container">
                        <div className="row-two">
                            <FormItem label="优惠券编码">
                                {getFieldDecorator(`paramValue`, {
                                    rules: [
                                        {required: true, message: '请输入优惠券编码'},
                                    ],
                                    initialValue: item.dynamicParamValueDTOList ? item.dynamicParamValueDTOList[0].paramValue : ''
                                })(<Input placeholder="" onBlur={this.clickFunEdit.bind(this)}/>)}
                            </FormItem>
                            <FormItem label="奖品展示名称">
                                {getFieldDecorator(`prizeName`, {
                                    rules: [
                                        {required: true, message: '请输入奖品展示名称'}],
                                    initialValue: item.prizeName,
                                    getValueFromEvent: (event) => {
                                        return event.target.value.substring(0,8);
                                    }
                                })(<Input placeholder="" onBlur={this.clickFunEdit.bind(this)} onChange={this.handleTitle.bind(this)}/>)}
                            </FormItem>
                        </div>
                        <div className="row-two">
                            <FormItem label="优惠券展示金额">
                                {getFieldDecorator(`amount`, {
                                    rules: [{
                                        required: false,
                                        message: '请输入优惠券展示金额'
                                    },],
                                    initialValue: item.amount || couponData.couponAmount,
                                    getValueFromEvent: (event) => {
                                        //只能是数字 小数点后保留2位
                                        return event.target.value.replace(/^\D*([1-9]\d*\.?\d{0,2})?.*$/,'$1').substring(0,11);
                                    }
                                })(<Input placeholder="" onBlur={this.clickFunEdit.bind(this)}/>)}
                            </FormItem>
                            <FormItem label="优惠券告警数量">
                                {getFieldDecorator(`total`, {
                                    rules: [{
                                        required: false,
                                        message: '请输入优惠券告警数量'
                                    },],
                                    initialValue: item.total,
                                    getValueFromEvent: (event) => {
                                        return event.target.value.replace(/\D/g, '').substring(0,8);
                                    }
                                })(<Input placeholder="" onBlur={this.clickFunEdit.bind(this)}/>)}
                            </FormItem>
                        </div>
                        <div className="quan-show">
                            <div className="show-item">
                                <div className="item-label">优惠券类型</div>
                                <div className="item-txt">
                                    {utils.couponType(couponData.bonusTrigerId)}
                                </div>
                            </div>
                            <div className="show-item">
                                <div className="item-label">优惠券数量</div>
                                <div className="item-txt">{couponData.avtivityTimesLimit}</div>
                            </div>
                            <div className="show-item">
                                <div className="item-label">优惠券发起方</div>
                                <div className="item-txt">
                                    {utils.couponInitiator(couponData.couponInitiator)}
                                </div>
                            </div>
                        </div>
                        <div className="quan-show">
                            <div className="show-item">
                                <div className="item-label">优惠券密匙</div>
                                <div className="item-txt">{couponData.activitySecretKey}</div>
                            </div>
                            <div className="show-item">
                                <div className="item-label">优惠券使用时间</div>
                                <div className="item-txt">{
                                    couponData.startTime&&couponData.endTime ? 
                                    `${couponData.startTime} ~ ${couponData.endTime}` 
                                    : ''}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Form>
        );
    }
}

export default Form.create()(tabFormChild);