import React from 'react';
import { Modal, Button } from 'antd';
import './preview-popup.less';
import { service } from '../../service/service';
import { services } from '../../intergrade/service';
import QRCode from 'qrcode.react';

class Popup extends React.Component {
  state = { 
    visible: true ,
    preGameDetail:{},
    previewUrl:'',
    message:false,
  }

  showModal = () => {
    this.setState({
      visible: true,
      gameDetail: {},

    });
  };
  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
    this.props.handleClose();
  };
  componentWillMount() {
    let params = {
      activityCode:this.props.record.activityCode,
      thirdActivityCode:this.props.record.thirdActivityCode,
      
    }
    
    services.service.getPreProp(params).then(res => {
      console.log("预览弹窗接口",res)
      if(res&&res.data&&res.data.result){
        this.setState({ preGameDetail: res.data.result });
      }

    }).catch((e) => {
      console.log("失败：", e)
    });


    let duibaparams = {
      activityCode:this.props.record.activityCode,
      activityUrlType:2,
      gameId:this.props.record.gameId,
   
   }
    services.service.getDuiBa(duibaparams).then(res => {
      console.log("duiba", res)
       if(res&&res.data&&res.data.msg&&res.data.msg=="失败"){
        this.setState({ message: true });
       }else if(res&&res.data&&res.data.msg&&res.data.msg=="成功"){   
        this.setState({ previewUrl: res.data.result})
       }   
    }).catch((e) => {
       console.log("失败：", e)
    });


  }

  render() {
    let preGameData = this.state.preGameDetail;
    let previewUrlMa = this.state.previewUrl;
    {
    return (
      <div>
        <Button type="primary" onClick={this.showModal}>
          预览
        </Button>
        <Modal
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={null}
          width={690}

        >
         <div className="popup-box"> 
          <div className="popup-content"> 
              <div className="popup-leftBox">  
                 <img className="leftBox-img" src={preGameData.imageLarge} />
              </div>   
              <div className="popup-rightBox"> 
                 <div className="rightBox-content">  
                     <div className="Scan-box">
                           { this.state.message &&
                                  <div className="preMessage">活动信息获取失败,请稍后再试!</div>
                           }
                           {
                             previewUrlMa&&
                            <QRCode value={previewUrlMa} size={170} />
                            }
                              {
                             previewUrlMa&&
                             <div className="wxScan"><span className="scan">微信扫一扫试玩</span></div> 
                            }
                       
                     </div>
                 
                 </div>    
              </div>   
        </div> 
        </div>  
        </Modal>
      </div>
    );

    }
  }
}

export default Popup;