import React, { Component } from 'react';
import './index.less';
import {services}  from '../../../intergrade/service'
class Activity extends Component {

  state = {
    message: false,
    duibaUrl: "",
  };
  componentWillMount() {
  
      console.log('请求兑吧免登入参',this.props.actData)
      let actData = this.props.actData
      services.service.getDuiBa(actData).then(res => {
    
        console.log("兑吧", res)
         if(res&&res.data&&res.data.msg&&res.data.msg=="失败"){
          this.setState({ message: true });
         }else if(res&&res.data&&res.data.msg&&res.data.msg=="成功"){
          this.setState({ duibaUrl:res.data.result });
         }

           
      }).catch((e) => {
        console.log("失败：", e)
      });
  }


  render() {
    var duibaUrl=this.state.duibaUrl;
    return (
      
      <div className="Activity">
        { this.state.message&&
             <div className="ActivityMessage">活动信息获取失败,请稍后再试!</div>
            }
     
        {
          this.state.duibaUrl&&
           <iframe src={duibaUrl} style={{border:0,width:"100%",height:980}} ></iframe> 
        }
        
     
      </div>
    );
  }
}

export default Activity;