// import { Event } from './common/Event';
import { utils } from './common/Utils';
import React, { Component } from 'react';
import { createHashHistory } from 'history';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import Login from './pages/login';
import Home from './pages/home';
import Edit from './pages/edit';
import MyActive from './pages/home/MyActive';
import NewActive from './pages/home/NewActive';
import { ConfigProvider } from 'antd';
import zh_CN from 'antd/es/locale-provider/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
moment.locale('zh-cn');


export default class ERouter extends Component {
  constructor() {
    super();
    utils.addEventListener('goLogin', this.goLogin, this);
  }
  goLogin(e) {
    const history = createHashHistory();
    history.push(`/login?${encodeURIComponent('backUrl')}=${encodeURIComponent(e.data)}`);
  }
  render() {
    return (
      <ConfigProvider locale={zh_CN}>
        <Router>
          <Switch>
            <Route path="/login" component={Login} />
            <Route path="/edit" component={Edit} />
            <Route path="/"
              render={() =>
                <Home>
                  <Switch>
                    <Route path="/newActive" component={NewActive} />
                    <Route path="/myActive" component={MyActive} />
                    <Redirect to="/newActive" />
                  </Switch>
                </Home>
              }
            />
          </Switch>
        </Router>
      </ConfigProvider>
    );
  }
}
