import React, { Component } from 'react';
import './index.less';
import LoginForm from './LoginForm';
import { utils } from '../../common/Utils';
class Login extends Component {
  constructor(props) {
    super(props)
     this.state = {
      backUrl: utils.keyValuetoJson(props.location.search).backUrl
    }
  }
  render() {
    return (
      <div className="register-bg">
        <div className="register-header"><div className="header-img"></div></div>
        <div className="register-container">
          <LoginForm backUrl={this.state.backUrl}></LoginForm>

        </div>
        {/* <div className="footer-text">
          <span> 粤公网安备44010502000715号  | Copyright © 2010-2019 广州凡科互联网科技股份有限公司  粤ICP备10235580号 </span>
        </div> */}
      </div>
    );
  }
}

export default Login;
