import React, { Component } from 'react';
import './index.less';
import Award from './Award';
import Activity from './Activity';
import Share from './Share';
// import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { createHashHistory } from 'history';
import { service } from '../../service/service';
import { utils } from '../../common/Utils';

import { Layout, Button, Tabs, message } from 'antd';

const { Content, Sider } = Layout;
const { TabPane } = Tabs;


class Edit extends Component {
  constructor(props){
    super(props)
    console.log("游戏Id:",utils.keyValuetoJson(this.props.location.search))
  }

  state = {
    // conLeftWidth: 360,
    actData: utils.keyValuetoJson(this.props.location.search),
    activityCode: utils.keyValuetoJson(this.props.location.search).activityCode
  }

  componentWillMount () {
    console.log('state',this.state)
  }

  tabChange = key => {
    // if (key === '2') {
    //   this.setState({
    //     conLeftWidth: 0,
    //   })
    // } else {
    //   this.setState({
    //     conLeftWidth: 360,
    //   })
    // }
  }
  // 查询是否有分享信息
  async queryGameShareInfo(data) {
    let res = await service.queryGameShareInfo(data);
    if (res.data && res.data.resultCode === 'GMSH01' && res.data.result) {
      message.success('保存成功！')
    } else {
      message.error('请检查分享设置！')
    }
  }

  saveClick = e => {
    console.log(this.shareFormRef)
    if (this.shareFormRef) {
      this.shareFormRef.loginSubmit();
    } else {
      // tab切下分享未加载时 拿不到子组件ref
      this.queryGameShareInfo({
        activityCode: this.state.activityCode,
      })
      
    }
  }

  close () {
    const history = createHashHistory();
    history.push('/myActive');
  }

  render() {
    return (
      <div className="Edit">
        <div className="edit-header">
          <div className="logo"></div>
          <Button className="save-btn" icon="save" onClick={this.saveClick}>
            {/* <div className="icon-save"></div> */}保 存
          </Button>
          <Button className="close-btn" icon="close" onClick={this.close}>
            {/* <div className="icon-close"></div> */}关 闭
          </Button>
        </div>
        <Layout className="content">
          {/* 830左边不展示 */}
          {/* <Sider width={170} style={{background:'#fff'}}>
            <div className="side-cont">
              <div className="side-item active">首页</div>
              <div className="side-item">奖品详情</div>
              <div className="side-item">中奖页面</div>
              <div className="side-item">未中奖页</div>
              <div className="side-item">分享效果</div>
            </div>
          </Sider> */}
          <Content>
            {/* <div className="content-left" style={{width: this.state.conLeftWidth}}></div> */}
            <div className="content-edit">
              <Tabs defaultActiveKey="1" animated={false} tabBarGutter={70} onChange={this.tabChange}>
                <TabPane tab="奖项设置" key="1">
                  <Award activityCode={this.state.activityCode}></Award>
                </TabPane>
                <TabPane tab="活动编辑" key="2">
                  <Activity actData={this.state.actData} />
                </TabPane>
                <TabPane tab="分享设置" key="3">
                  <Share 
                    wrappedComponentRef={(form) => this.shareFormRef = form}
                    activityCode={this.state.activityCode}
                  />
                  {/* <EnhancedForm wrappedComponentRef={(form) => this.form = form} /> */}
                </TabPane>
              </Tabs>
            </div>
          </Content>
        </Layout>
      </div>
    );
  }
}

export default Edit;